@import '../../../../styles/utils';

.navbar {
  display: flex;
  justify-content: space-between;
  gap: 64px;

  font-size: 12px;
  font-weight: 700;
  line-height: 11px;
  letter-spacing: 0.04em;
  text-align: left;

  &__link{
    height: 64px;

    text-decoration: none;
    text-transform: uppercase;
    color: $color-secondary;
    outline: none;


    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
}
